body {
  background: yellow;
  margin: 0 auto;
  max-width: 1600px;
  font-family: "Press Start 2P", cursive;
  font-size: 36px;
}
body #image-wrapper {
  display: grid;
  width: 1500px;
  margin: 0 auto;
  grid-template-columns: repeat(5, 20%);
  grid-template-rows: repeat(5, 20%);
  height: 92vh;
}
body #image-wrapper .computer {
  grid-column-start: 3;
  grid-row-start: 1;
  align-self: end;
  margin-bottom: 20px;
}
body #image-wrapper .computer span {
  margin-bottom: 20px;
}
body #image-wrapper #computerScore, body #image-wrapper #userScore {
  display: flex;
  justify-content: center;
}
body #image-wrapper .user {
  grid-column-start: 3;
  grid-row-start: 4;
  align-self: end;
  justify-self: center;
  margin-bottom: 20px;
}
body #image-wrapper .item {
  height: 220px;
  width: 255px;
  cursor: pointer;
  justify-self: center;
  transition: 0.2s all;
}
body #image-wrapper .item:hover {
  transform: scale(1.05);
}
body #image-wrapper #rock {
  background: url(/img/rock.png);
  background-repeat: no-repeat;
  background-position: center;
  grid-column-start: 2;
  grid-row-start: 5;
}
body #image-wrapper #paper {
  background: url(/img/paper.png);
  background-repeat: no-repeat;
  background-position: center;
  grid-column-start: 3;
  grid-row-start: 5;
}
body #image-wrapper #scissors {
  background: url(/img/scissors.png);
  background-repeat: no-repeat;
  background-position: center;
  grid-row-start: 5;
  grid-column-start: 4;
}
body #image-wrapper .computerrock {
  background: url(/img/rock.png);
  background-repeat: no-repeat;
  grid-row-start: 2;
  grid-column-start: 3;
}
body #image-wrapper .computerpaper {
  background: url(/img/paper.png);
  background-repeat: no-repeat;
  grid-row-start: 2;
  grid-column-start: 3;
}
body #image-wrapper .computerscissors {
  background: url(/img/scissors.png);
  background-repeat: no-repeat;
  grid-row-start: 2;
  grid-column-start: 3;
}